import React, { useEffect } from 'react';
import { navigate } from "gatsby"

import { fbLogout, logout } from '../../services/auth';
import { SESSION_PROVIDER } from '../Session';
import { PATH_LANDING } from '../../constants/routes';

interface SignOutRedirectProps {
  goto?: string;
}

const SignOutRedirect: React.FunctionComponent<SignOutRedirectProps> = ({
  goto
}) => {
  useEffect(() => {
    SESSION_PROVIDER === "fb" ? fbLogout() : logout();
    navigate(goto || PATH_LANDING); //, {replace: true});
  });

  return (
    <>
      <h5>Logging out...</h5>
    </>
  );
}

export { SignOutRedirect };
